import React, {useEffect, useState} from 'react';
import {makeStyles} from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import {AppBar, Button, IconButton, Paper, Toolbar, Typography} from "@material-ui/core";
import Logo from "../../assets/svg/logo";
import "./navBar.css";

// const [logoWidth, setLogoWidth] = useState(250)
// const [logoHeight, setLogoHeight] = useState(86.193)
const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
        // backgroundColor: "#220480",
        minHeight: "15vh",

    },
    menuButton: {
        marginRight: theme.spacing(10),
    },
    title: {
        flexGrow: 1,
        // margin:"20px",
        // backgroundColor:"rgba(17,17,17,0.63)"
    },
    appBar: {

        backgroundColor:"transparent",
        boxShadow: "none",
        transition:" 1s ease",
        [theme.breakpoints.up("md")]: {
            borderBottom: "2px solid rgba(255,255,255,.05)",
            padding:"20px 0",
        },
    },
    setAppBar:{
        borderBottom: "0px solid rgba(255,255,255,.05)",
        backgroundColor:"rgb(255,255,255)",
        transition:" 1s ease",
        [theme.breakpoints.up("md")]: {
            borderBottom: "2px solid rgba(255,255,255,.05)",
        },
    },
    navBtn:{
        transition:" 1s ease",
        fontFamily:['Rubik', 'sans-serif'].join(','),
        fontWeight:400,
        margin:"5px",
        padding:"10px 25px" ,
        borderRadius:"50px",

    },
    setNavBtn:{
        transition:" 1s ease",
        fontFamily:['Rubik', 'sans-serif'].join(','),
        fontWeight:400,
        margin:"5px",
        padding:"10px 25px" ,
        borderRadius:"50px",
        color:"#000000",

    },
    navContactBtn:{
        transition:" 1s ease",
        fontFamily:['Rubik', 'sans-serif'].join(','),
        fontWeight:400,
        margin:"10px",
        padding:"10px 25px" ,
        borderRadius:"50px",
        border:"3px solid white"
    },
    setNavContactBtn:{
        transition:" 1s ease",
        fontFamily:['Rubik', 'sans-serif'].join(','),
        fontWeight:400,
        margin:"10px",
        padding:"10px 25px" ,
        borderRadius:"50px",
        border:"3px solid black",
        color:"#000"
    },

}));

function NavBar(props) {
    const classes = useStyles();
    const [navBackground, setNavBackground] = useState("appBar");
    const [navBtn, setNavBtn] = useState("navBtn");
    const [navContactBtn, setNavContactBtn] = useState("navBtn");

    const navBackgroundRef = React.useRef();
    navBackgroundRef.current = navBackground;

    const navBtnRef = React.useRef();
    navBtnRef.current = navBtn;

    const navContactBtnRef = React.useRef();
    navContactBtnRef.current = navContactBtn;

    useEffect(() => {
        const handleScroll = () => {
            const show = window.scrollY > 0;
            if (show){
                setNavBackground("setAppBar");
                setNavBtn("setNavBtn");
                setNavContactBtn("setNavContactBtn");
                // setNavBarAnim("fixed")
            }else{
                setNavBackground("appBar");
                setNavBtn("navBtn");
                setNavContactBtn("navContactBtn");
                // setNavBarAnim("static")
            }
        }
        document.addEventListener("scroll", handleScroll);

        return () => {
            document.removeEventListener("scroll", handleScroll);
        }
    }, []);

    return (
        <div className={classes.root}>
            <AppBar position={"fixed"} className={classes[navBackgroundRef.current]}>
                <Toolbar>
                    {/*<IconButton edge="start" className={classes.menuButton} color="inherit" aria-label="menu">*/}
                    {/*    <MenuIcon />*/}
                    {/*</IconButton>*/}
                    <Typography className={classes.title}>
                        <Button color="inherit">
                        <Logo/>
                        </Button>
                    </Typography>
                    <Button color="inherit" className={classes[navBtnRef.current]}>Home</Button>
                    <Button color="inherit" className={classes[navBtnRef.current]}>What We Do</Button>
                    <Button color="inherit" className={classes[navBtnRef.current]}>Our Work</Button>
                    <Button color="inherit" className={classes[navBtnRef.current]}>Join Us</Button>
                    <Button color="inherit" className={classes[navBtnRef.current]}>Blog</Button>
                    <Button color="inherit" className={classes[navBtnRef.current]}>About</Button>
                    <Button color="inherit" className={classes[navContactBtnRef.current]}>Contact</Button>
                </Toolbar>
            </AppBar>
        </div>
    );
}

export default NavBar;