import React, {useEffect, useState} from 'react';
import {makeStyles} from "@material-ui/core";


const useStyles = makeStyles((theme) => ({
    logo:{
        width:"250px",
        height:"86.193px",
        [theme.breakpoints.down("md")]: {
            width:"300px",
            height:"86.193px"
        },
        [theme.breakpoints.down("sm")]: {
            width:"300px",
            height:"86.193px"
        },
        [theme.breakpoints.down("xs")]: {
            width:"150px",
            height:"86.193px"
        },
    },
    logoColor:{
        fill:"#ffffff",
        transition:" 1s ease",
    },
    setLogoColor:{
        fill:"#000000",
        transition:" 1s ease",
    },

}));
function Logo(props) {
    const classes = useStyles();
    const [logoColor, setLogoColor] = useState("logoColor");

    const logoColorRef = React.useRef();
    logoColorRef.current = logoColor;

    useEffect(() => {
        const handleScroll = () => {
            const show = window.scrollY > 0;
            if (show){
                setLogoColor("setLogoColor");
                // setNavBarAnim("fixed")
            }else{
                setLogoColor("logoColor");
                // setNavBarAnim("static")
            }
        }
        document.addEventListener("scroll", handleScroll);

        return () => {
            document.removeEventListener("scroll", handleScroll);
        }
    }, []);
    return (
        <svg
            className={classes.logo}
            xmlns="http://www.w3.org/2000/svg"
            // width={480.108}
            // height={86.193}
            viewBox="0 0 480.108 86.193"
            {...props}
        >
            <defs>
                <linearGradient
                    id="prefix__a"
                    y1={0.5}
                    x2={1}
                    y2={0.5}
                    gradientUnits="objectBoundingBox"
                >
                    <stop offset={0} stopColor="#e605f9" />
                    <stop offset={0.49} stopColor="#7379f9" />
                    <stop offset={0.51} stopColor="#6f7ff9" />
                    <stop offset={1} stopColor="#08f3fc" />
                </linearGradient>
            </defs>
            <g data-name="Group 4">
                <g data-name="Group 1" fill="url(#prefix__a)">
                    <path
                        data-name="Path 1"
                        d="M16.442 60.964a3.576 3.576 0 01.827.1l12.387-17.978L.068 0h37.781l10.733 15.576L59.358 0h37.766l-7.978 11.618-1.995 2.9H64.875L48.563 38.096 32.316 14.518h-6.759l19.626 28.579-13.372 19.414a3.6 3.6 0 11-2.111-1.446L42.079 43.1 20.461 11.619h12.963l15.144 21.977 15.2-21.977h22.27l5.983-8.713h-31.56L48.578 20.081 36.742 2.904H5.166l27.6 40.184L19.38 62.511a3.565 3.565 0 11-2.938-1.546z"
                    />
                    <path
                        data-name="Path 2"
                        d="M80.681 25.229a3.576 3.576 0 01-.827-.1L67.467 43.107l29.588 43.086h-37.78L48.542 70.617 37.766 86.193H.004l7.978-11.618 1.995-2.9h22.275l16.312-23.578 16.24 23.574h6.759L51.937 43.092l13.375-19.411a3.6 3.6 0 112.111 1.446L55.044 43.092l21.618 31.481H63.704L48.56 52.596l-15.2 21.977H11.09l-5.983 8.713h31.564l11.883-17.177 11.835 17.177h31.576l-27.6-40.185 13.378-19.42a3.565 3.565 0 112.938 1.546z"
                    />
                </g>
                <g data-name="Group 3" className={classes[logoColorRef.current]}>
                    <g data-name="Group 2">
                        <path
                            data-name="Path 3"
                            d="M394.186 63.76h-48.89v7.35h44.673a4.184 4.184 0 004.154-4.155v-3.2z"
                        />
                        <path
                            data-name="Path 5"
                            d="M394.186 39.349h-48.89v7.35h44.673a4.187 4.187 0 004.154-4.154v-3.2z"
                        />
                        <path
                            data-name="Path 7"
                            d="M394.186 14.933h-48.89v7.35h44.673a4.184 4.184 0 004.154-4.155v-3.2z"
                        />
                    </g>
                    <path
                        data-name="Path 9"
                        d="M207.673 14.917a28.185 28.185 0 000 56.37 28.62 28.62 0 0028.183-28.439 28.136 28.136 0 00-28.183-27.931zm.382 49.273a21.185 21.185 0 1121.09-21.279 21.4 21.4 0 01-21.09 21.279zm0 0"
                    />
                    <path
                        data-name="Path 10"
                        d="M152.895 17.777v-2.813H125.86c-16.1 0-29.2 12.719-29.2 27.994 0 15.464 12.908 27.8 29.015 28.183H152.9v-2.812a4.187 4.187 0 00-4.154-4.155h-23.07c-12.143-.383-21.859-9.523-21.859-21.283s9.717-20.834 21.859-21.09h23.068a4.114 4.114 0 004.154-4.023zm0 0"
                    />
                    <path
                        data-name="Path 11"
                        d="M262.457 17.762a4.114 4.114 0 004.154 4.024h23.071c12.143.256 21.859 9.334 21.859 21.09s-9.717 20.9-21.859 21.284h-23.071a4.187 4.187 0 00-4.154 4.154v2.813h27.225c16.107-.382 29.015-12.719 29.015-28.183 0-15.275-13.1-27.994-29.2-27.994h-27.04v2.813zm0 0"
                    />
                    <path
                        data-name="Path 12"
                        d="M480.109 71.098l-23.646-26.842a2.107 2.107 0 010-2.556l23.282-26.428a.249.249 0 00-.186-.414h-9.244a.252.252 0 00-.186.084L451.67 35.755a2.238 2.238 0 01-3 0l-18.209-20.757a.252.252 0 00-.187-.085h-9.239a.249.249 0 00-.186.414l23.471 26.428a2.107 2.107 0 010 2.556l-23.282 26.428a.249.249 0 00.187.413h9.242a.249.249 0 00.187-.085l18.4-21.005a2.25 2.25 0 013 0l18.387 21a.251.251 0 00.2.084c1.1-.052 9.469-.052 9.469-.052zm0 0"
                    />
                </g>
            </g>
        </svg>
    )
}

export default Logo;